import React from 'react'
import {Card,Accordion,Button} from 'react-bootstrap'
import './myStyles.css'
import Img from '../static/Topslip2.jpg'
import Img2 from '../static/topslip.jpg'
import top from '../static/Topslip.jps.jpg'
import top1 from '../static/topslip4.jpg'
import top2 from '../static/topslip5.jpg'
import styled from 'styled-components'
const H1=styled.h1`
color:#000;
`

const P=styled.p`
    text-align:justify;
`

function TopSlip() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12" id="topslip">
                <Accordion defaultActiveKey="0">
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            <H1>TOPSLIP</H1>
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body><P>Topslip is located near Pollachi in the state of Tamil Nadu, India, at an altitude of 800 feet from the sea level on the Anamalai mountain range.
                             It is a part of the protected Indira Gandhi Wildlife Sanctuary. Topslip is blessed with a gentle altitude, salubrius climate and a vareity of flora and fauna.
                              Its picturesque location and fabulous scenery has made it a popular location for several movie sequences. It stands majestically with Green Spread Mountains and forest all around. 
                              It is a Project Elephant sanctuary and a Project Tiger sanctuary. The local administrative control is with the Forest Range Officer under supervision from the Wildlife Warden at Pollachi</P></Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                        Click to view Pictures
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <img src={Img} fluid></img>
                            <img src={Img2} fluid></img>
                            <img src={top} fluid></img>
                            <img src={top1} fluid></img>
                            <img src={top2} fluid></img>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
                </div>
            </div>
        
        </div>
        
    )
}

export default TopSlip
