import React from 'react'
import {Card,Accordion,Button} from 'react-bootstrap'
import './myStyles.css'
import Aazhi from '../static/Aliyar-TopView.jpg'
import Aazhi1 from '../static/Azhiyar_Dam.jpg'
import monkey from '../static/monkey-falls.jpg'
import park from '../static/park.jpg'
import temple from '../static/temple.jpg'
import dam from '../static/Aliyardam.jpg'
import styled from 'styled-components'
const H1=styled.h1`
color:#000;
`

const P=styled.p`
    text-align:justify;
`

function Aazhiyar() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12" id="topslip">
                <Accordion defaultActiveKey="0">
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                           <H1>AAZHIYAR</H1> 
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body><P>Aliyar (ஆழியாறு) (also spelt Azhiyar / Aaliyar) Reservoir is a 6.48 km2 (2.5 sq mi) reservoir located in 
                            Aliyar village near Pollachi town in Coimbatore District, Tamil Nadu, South India. The dam is located in the foothills of Valparai,
                             in the Anaimalai Hills of the Western Ghats. It is about 65 kilometres (40 mi) from Coimbatore. 
                             The dam offers some ideal getaways including a park, garden, aquarium, play area and a mini Theme-Park maintained by Tamil Nadu Fisheries Corporation for visitors enjoyment.
                             The scenery is beautiful, with mountains surrounding three quarters of the reservoir. Boating is also available.</P></Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                            Click to view Pictures
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <img src={Aazhi}/>
                            <img src={Aazhi1}/>
                            <img src={monkey}/>
                            <img src={park}/>
                            <img src={temple}/>
                            <img src={dam}/>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
                </div>
            </div>
        
        </div>
    )
}

export default Aazhiyar
