import React from 'react'
import {Card,Accordion,Button} from 'react-bootstrap'
import './myStyles.css'
import balloon from '../static/Balloonfest.jpg'
import fest from '../static/festBalloon.jpg'
import ball from '../static/BALLOON.jfif'
import styled from 'styled-components'
const H1=styled.h1`
color:#000;
`
const P=styled.p`
    text-align:justify;
`

function BalloonFestival() {
    return (
        <div className="container">
            <div className="row">
                <div className="col-sm-12" id="topslip">
                <Accordion defaultActiveKey="0">
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="0">
                            <H1>BALLOON FESTIVAL</H1>
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="0">
                        <Card.Body><P>The three-day event this year has 10 hot air balloons from eight countries. Apart from taking a ride in the balloons, visitors can participate in games, 
                            witness cultural performances, and enjoy food at the multiple outlets.
                             This year’s attractions are special shape balloons - white CAT from Germany, ice cream from Belguim, and Angry Birds from the US to name a few.
The pilots for the balloons are from the European countries and the U.S. This is the fifth year of the Festival and nearly 45,000 visitors are expected, according to Benedict Savio, 
founder director of Global Media Box, which is organising the Festival in association with The Slaves Restaurant and the support of the Department of Tourism, Tamil Nadu.</P></Card.Body>
                        </Accordion.Collapse>
                    </Card>
                    <Card>
                        <Card.Header>
                        <Accordion.Toggle as={Button} variant="link" eventKey="1">
                        Click to view Pictures
                        </Accordion.Toggle>
                        </Card.Header>
                        <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <img src={balloon}/>
                            <img src={fest}/>
                            <img src={ball}/>
                        </Card.Body>
                        </Accordion.Collapse>
                    </Card>
                </Accordion>
                </div>
            </div>
        
        </div>
    )
}

export default BalloonFestival
